.Mobile-nav-div {
  position: fixed;
  z-index: 10;
  inset: 0;
  background-color: var(--main-color);
  transition: 0.4s all;
  transform: translateY(100vh);
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav__mobile_links_button_contener,
.nav__mobile_links_div {
  font-size: 17px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.nav__mobile_links_button_contener {
  width: 100%;
  position: relative;
  top: -4rem;
}
.nav__mobile_button {
  position: relative;
  top: 1rem;
  width: 70%;
}

.nav__mobile_links_div a {
  padding-block: 15px;
}
